:root {
  /* Colores principales */
  --color-principal: #23415b;      /* Azul principal */
  --color-secundario: #6c757d;     /* Gris secundario */
  --color-fondo: #ffffff;          /* Blanco de fondo */
  --color-texto: #333333;          /* Gris oscuro para texto */
  --color-enlace: #0056b3;         /* Azul oscuro para enlaces */
  --color-exito: #28a745;          /* Verde para mensajes de éxito */
  --color-error: #dc3545;          /* Rojo para mensajes de error */

  /* Fuentes */
  --fuente-principal: 'Arial', sans-serif;
  --fuente-secundaria: 'Verdana', sans-serif;

  /* Tamaños de fuente */
  --tamano-fuente-base: 16px;
  --tamano-fuente-titulo: 24px;
  --tamano-fuente-subtitulo: 20px;

  /* Espaciado */
  --espaciado-pequeno: 8px;
  --espaciado-mediano: 16px;
  --espaciado-grande: 24px;

  /* Bordes */
  --borde-radio: 4px;
  --borde-ancho: 1px;
  --borde-color: #cccccc;

  /* Sombras */
  --sombra-caja: 0 2px 4px rgba(102, 59, 59, 0.1);
}

/* Estilos globales */
body {
  background-color: var(--color-fondo);
  color: var(--color-texto);
  font-family: var(--fuente-principal);
  margin: 0;
  padding: 0;
}

/* Para asegurar que el contenedor no genere scroll horizontal */
.home-main {
  width: 100%;
  overflow-x: hidden;
}

/* ======================== HERO SECTION ======================== */
.hero-section {
  background-color: var(--color-fondo);
  padding: 60px 20px;
  color: var(--color-texto);
  text-align: center;
}

.hero-section h2 {
  font-size: 2.5rem; /* O usa calc(var(--tamano-fuente-titulo) * 1.2) */
  margin-bottom: 20px;
}

.hero-section p {
  font-size: 1.25rem; /* O var(--tamano-fuente-subtitulo) */
  max-width: 800px;
  margin: 0 auto;
  line-height: 1.6;
}

/* ======================== ABOUT SECTION ======================== */
.about-section {
  padding: 60px 20px;
  text-align: center;
  color: var(--color-texto);
}

.about-section h2 {
  font-size: 2rem;
  margin-bottom: 20px;
  color: var(--color-principal); /* Antes era var(--dorado-suave) */
}

.about-section p {
  font-size: 1.125rem;
  max-width: 800px;
  margin: 0 auto 40px;
  line-height: 1.6;
}

.about-images {
  display: flex;
  justify-content: center;
  gap: 20px;
  flex-wrap: wrap;
}

/* Videos grandes y centrados */
.about-images video {
  width: 100%;
  max-width: 600px;
  border-radius: var(--borde-radio);
  box-shadow: var(--sombra-caja);
}

.about-videos {
  display: flex;
  justify-content: center;
  gap: 20px;
  flex-wrap: wrap;
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
  border-radius: var(--borde-radio);
  box-shadow: var(--sombra-caja);
}

/* ======================== SERVICES SECTION ======================== */
.services-section {
  background-color: var(--color-secundario); /* Antes era var(--gris-perla) */
  padding: 60px 20px;
  color: var(--color-fondo); /* Texto blanco */
  text-align: center;
}

.services-section p {
  font-size: 1.125rem;
  max-width: 800px;
  margin: 0 auto;
  line-height: 1.6;
}

/* ======================== EXTRA LINKS ======================== */
.extra-links {
  text-align: center;
  padding: 40px 20px;
}

/* Botón con efecto de destello y pulsación */
.extra-links Link {
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  text-decoration: none;
  transition: background-color 0.3s ease;
}

.extra-links Link:hover {
  background-color: #0056b3;
}



/* ======================== PRODUCTS SECTION ======================== */
.products-section {
  padding: 60px 20px;
  text-align: center;
  color: var(--color-texto);
}

.products-section h2 {
  font-size: 2rem;
  margin-bottom: 20px;
  color: var(--color-principal); /* Antes era var(--dorado-suave) */
}

.products-section p {
  font-size: 1.125rem;
  max-width: 800px;
  margin: 0 auto;
  line-height: 1.6;
}

/* ======================== WHATSAPP INVITE ======================== */
.whatsapp-invite-container {
  text-align: center;
  margin: 40px 20px;
}

.whatsapp-invite-link {
  background-color: var(--color-exito); /* Antes var(--dorado-suave) */
  color: var(--color-fondo);           /* Texto blanco */
  padding: 12px 24px;
  border-radius: 9999px;
  text-decoration: none;
  font-size: 1.125rem;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.whatsapp-invite-link:hover {
  background-color: var(--color-enlace); /* Antes var(--brand-hover) */
  transform: scale(1.05);
}

/* ======================== RESPONSIVE ======================== */
@media (max-width: 768px) {
  .hero-section h2 {
    font-size: 2rem;
  }

  .hero-section p,
  .about-section p,
  .products-section p {
    font-size: 1rem;
    padding: 0 10px;
  }

  .about-images,
  .about-videos {
    flex-direction: column;
  }

  .contact-map {
    height: 300px;
  }
}

@media (max-width: 480px) {
  .hero-section h2 {
    font-size: 1.75rem;
  }

  .hero-section p {
    font-size: 0.95rem;
  }

  .about-section h2,
  .products-section h2 {
    font-size: 1.75rem;
  }

  .map-info h2 {
    font-size: 1.75rem;
  }

  .map-info p {
    font-size: 1rem;
  }

  .contact-map {
    height: 250px;
  }
}
