/* ===================================
   VARIABLES GLOBALES
   =================================== */
   :root {
    /* Colores principales */
    --color-principal: #23415b;      /* Azul principal */
    --color-secundario: #6c757d;     /* Gris secundario */
    --color-fondo: #ffffff;          /* Blanco de fondo */
    --color-texto: #333333;          /* Gris oscuro para texto */
    --color-enlace: #0056b3;         /* Azul oscuro para enlaces */
    --color-exito: #28a745;          /* Verde para mensajes de éxito */
    --color-error: #dc3545;          /* Rojo para mensajes de error */
  
    /* Fuentes */
    --fuente-principal: 'Arial', sans-serif;
    --fuente-secundaria: 'Verdana', sans-serif;
  
    /* Tamaños de fuente */
    --tamano-fuente-base: 16px;
    --tamano-fuente-titulo: 24px;
    --tamano-fuente-subtitulo: 20px;
  
    /* Espaciado */
    --espaciado-pequeno: 8px;
    --espaciado-mediano: 16px;
    --espaciado-grande: 24px;
  
    /* Bordes */
    --borde-radio: 4px;
    --borde-ancho: 1px;
    --borde-color: #cccccc;
  
    /* Sombras */
    --sombra-caja: 0 2px 4px rgba(102, 59, 59, 0.1);
  }
  
  /* ===================================
     ESTILOS GLOBALES
     =================================== */
  body {
    margin: 0;
    padding: 0;
    font-family: var(--fuente-principal);
    background-color: var(--color-fondo);
    color: var(--color-texto);
  }
  
  .container {
    margin: 0 auto;
    padding: var(--espaciado-mediano);
    max-width: 600px;
  }
  
  /* ===================================
     SIGNUP BOX
     =================================== */
  .signup-box {
    background-color: var(--color-fondo);
    padding: var(--espaciado-mediano);
    width: 100%;
    max-width: 400px;
    margin: 0 auto;
    border-radius: var(--borde-radio);
    box-shadow: var(--sombra-caja);
  }
  
  .profile-pic-container {
    width: 80px;
    height: 80px;
    margin: 0 auto;
    position: relative;
    overflow: hidden;
    border-radius: 50%;
  }
  
  .profile-pic {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .upload-photo-label {
    text-align: center;
    font-size: 12px;
    background-color: var(--color-secundario);
    color: var(--color-fondo);
    padding: var(--espaciado-pequeno) 0;
    cursor: pointer;
    position: absolute;
    bottom: 0;
    width: 100%;
  }
  
  /* ===================================
     FORM CONTAINER
     =================================== */
  .form-container {
    padding-top: var(--espaciado-mediano);
    display: flex;
    flex-direction: column;
    gap: var(--espaciado-mediano);
  }
  
  .input-group {
    display: grid;
  }
  
  .input-wrapper {
    background-color: var(--color-secundario);
    border-radius: var(--borde-radio);
    padding: var(--espaciado-pequeno);
  }
  
  .input-field {
    width: 100%;
    border: none;
    background-color: transparent;
    outline: none;
    padding: var(--espaciado-pequeno);
    font-size: var(--tamano-fuente-base);
    color: var(--color-texto);
  }
  
  .toggle-password {
    cursor: pointer;
    font-size: calc(var(--tamano-fuente-base) + 8px);
    color: var(--color-texto);
  }
  
  /* ===================================
     BOTÓN SIGNUP
     =================================== */
  .signup-button {
    background-color: var(--color-principal);
    color: var(--color-fondo);
    padding: var(--espaciado-pequeno) var(--espaciado-mediano);
    width: 100%;
    max-width: 150px;
    border-radius: 9999px;
    margin: var(--espaciado-grande) auto 0;
    display: block;
    transition: all 0.3s ease;
    text-align: center;
    border: none;
    cursor: pointer;
  }
  
  .signup-button:hover {
    background-color: var(--color-enlace);
    transform: scale(1.05);
  }
  
  /* ===================================
     ENLACE A LOGIN
     =================================== */
  .login-link {
    color: var(--color-enlace);
    text-decoration: none;
  }
  
  .login-link:hover {
    text-decoration: underline;
    color: var(--color-principal);
  }
  