.vertical-card-container {
  max-width: 1200px;
  margin: 24px auto;
  padding: 0 16px;
}

@media (max-width: 768px) {
  .vertical-card-container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.vertical-card-heading {
  font-size: 24px;
  font-weight: 600;
  padding-bottom: 16px;
  color: #333333;
}

.vertical-card-content {
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* exactamente 3 tarjetas por fila */
  gap: 16px;
  justify-content: center;
}




.product-card {
  width: 100%; /* Se ajusta exactamente al espacio disponible en la columna grid */
}

.product-image {
  display: flex;
  object-fit: contain;
  justify-content: center;
  align-items: center;
}

.product-image img {
  object-fit: contain;
  transition: transform 0.3s ease;
}

.product-image img:hover {
  transform: scale(1.05);
}

.product-info {
  padding: 16px;
  display: flex;
  flex-direction: column;
  gap: 8px;
  flex-grow: 1;
}

.product-name {
  font-size: 18px;
  font-weight: 600;
  color: #333333;
  margin: 0;
}

.product-category,
.product-brand {
  font-size: 14px;
  color: #D4AF37;
  text-transform: capitalize;
  margin: 0;
}

.product-prices {
  display: flex;
  align-items: center;
  gap: 8px;
}

.product-selling-price {
  color: #C0C0C0;
  font-weight: 700;
  font-size: 16px;
}

.product-original-price {
  color: #a0aec0;
  text-decoration: line-through;
  font-size: 14px;
}

.product-actions {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: auto;
}

.add-to-cart-btn {
  background-color: #009739 ;
  color: #ffffff;
  border:  1.25rem #D4AF37;
  padding: 10px 16px;
  border-radius: 4px;
  font-size: 14px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  border: none;
}

.add-to-cart-btn:hover {
  background-color: #C0C0C0;
}

.loading {
  animation: pulse 1.5s ease-in-out infinite;
}

.product-image-placeholder {
  background-color: #e2e8f0;
  height: 200px;
  width: 100%;
}

.loading-placeholder {
  background-color: #e2e8f0;
  border-radius: 4px;
}

@keyframes pulse {
  0% {
      opacity: 1;
  }
  50% {
      opacity: 0.5;
  }
  100% {
      opacity: 1;
  }
}

@media (max-width: 768px) {
  .vertical-card-content {
    grid-template-columns: repeat(2, 1fr);
    justify-content: center;
  }
}

@media (max-width: 480px) {
  .vertical-card-content {
    grid-template-columns: 1fr;
    justify-content: center;
  }
}
