/* ===================================
   VARIABLES GLOBALES
   (Si las declaras en otro archivo,
   no las repitas aquí.)
   =================================== */
   :root {
    /* Colores principales */
    --color-principal: #23415b;      
    --color-secundario: #6c757d;     
    --color-fondo: #ffffff;          
    --color-texto: #333333;          
    --color-enlace: #0056b3;         
    --color-exito: #28a745;          
    --color-error: #dc3545;          
  
    /* Fuentes */
    --fuente-principal: 'Arial', sans-serif;
    --fuente-secundaria: 'Verdana', sans-serif;
  
    /* Tamaños de fuente */
    --tamano-fuente-base: 16px;
    --tamano-fuente-titulo: 24px;
    --tamano-fuente-subtitulo: 20px;
  
    /* Espaciado */
    --espaciado-pequeno: 8px;
    --espaciado-mediano: 16px;
    --espaciado-grande: 24px;
  
    /* Bordes */
    --borde-radio: 4px;
    --borde-ancho: 1px;
    --borde-color: #cccccc;
  
    /* Sombras */
    --sombra-caja: 0 2px 4px rgba(102, 59, 59, 0.1);
  }
  
  /* ===================================
     ESTILOS GLOBALES
     =================================== */
  body {
    margin: 0;
    padding: 0;
    font-family: var(--fuente-principal);
    background-color: var(--color-fondo);
    color: var(--color-texto);
  }
  
  .container {
    margin: 0 auto;
    padding: var(--espaciado-mediano);
    max-width: 600px;
  }
  
  /* ===================================
     LOGIN BOX
     =================================== */
  .login-box {
    background-color: var(--color-fondo);
    padding: var(--espaciado-mediano);
    width: 100%;
    max-width: 400px;
    margin: 0 auto;
    border-radius: var(--borde-radio);
    box-shadow: var(--sombra-caja);
  }
  
  .icon-container {
    width: 80px;
    height: 80px;
    margin: var(--espaciado-mediano) auto;
  }
  
  .icon-container img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
  
  /* ===================================
     FORM CONTAINER
     =================================== */
  .form-container {
    padding-top: var(--espaciado-mediano);
    display: flex;
    flex-direction: column;
    gap: var(--espaciado-mediano);
  }
  
  .input-group {
    display: grid;
  }
  
  .input-wrapper {
    padding: var(--espaciado-pequeno);
    border-radius: var(--borde-radio);
    background-color: var(--color-secundario);
  }
  
  .password-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .input-field {
    width: 100%;
    border: none;
    background-color: transparent;
    outline: none;
    padding: var(--espaciado-pequeno);
    font-size: var(--tamano-fuente-base);
    color: var(--color-texto);
  }
  
  .toggle-password {
    cursor: pointer;
    font-size: calc(var(--tamano-fuente-base) + 8px);
    color: var(--color-principal);
  }
  
  /* ===================================
     ENLACES Y BOTONES
     =================================== */
  .forgot-password-link {
    display: block;
    text-align: right;
    margin-top: var(--espaciado-pequeno);
    color: var(--color-enlace);
    text-decoration: none;
  }
  
  .forgot-password-link:hover {
    text-decoration: underline;
    color: var(--color-principal);
  }
  
  .login-button {
    background-color: var(--color-principal);
    color: var(--color-fondo);
    padding: var(--espaciado-pequeno) var(--espaciado-mediano);
    width: 100%;
    max-width: 150px;
    border-radius: 9999px;
    margin: var(--espaciado-grande) auto 0;
    display: block;
    transition: all 0.3s ease;
    text-align: center;
    border: none;
    cursor: pointer;
  }
  
  .login-button:hover {
    background-color: var(--color-enlace);
    transform: scale(1.05);
  }
  
  /* ===================================
     SIGNUP TEXT
     =================================== */
  .signup-text {
    margin-top: var(--espaciado-mediano);
    text-align: center;
  }
  
  .signup-link {
    color: var(--color-enlace);
    text-decoration: none;
  }
  
  .signup-link:hover {
    text-decoration: underline;
    color: var(--color-principal);
  }
  